import { defineComponent } from "vue";

export default defineComponent({
  name: "Login",
  methods: {
    login() {
      // @ts-expect-error test
      this.$auth.loginWithRedirect();
    },
    logout() {
      // @ts-expect-error test
      this.$auth.logout({
        returnTo: `${window.location.origin}/login`,
      });
    },
  },
});
